.wave-container>svg {
    -webkit-transform-origin: bottom;
            transform-origin: bottom;
    -webkit-animation: animateWave 2000ms cubic-bezier(0.56, 1, 0.48, 1) forwards;
            animation: animateWave 2000ms cubic-bezier(0.56, 1, 0.48, 1) forwards;
}
@-webkit-keyframes animateWave {
    0% {
        -webkit-transform: scale(1, 0);
                transform: scale(1, 0);
    }
}
@keyframes animateWave {
    0% {
        -webkit-transform: scale(1, 0);
                transform: scale(1, 0);
    }
}
.navbar-light .navbar-nav .nav-link{
    color: rgb(70, 69, 69);
    background-image: linear-gradient(currentColor, currentColor);
    background-position: 0% 100%;
    background-repeat: no-repeat;
    background-size: 0% 1.5px;
    transition: all .5s ease-in;
    font-weight: bold;
    
}
.navbar-brand a{
    color: rgb(70, 69, 69);
    background-image: linear-gradient(currentColor, currentColor);
    background-position: 0% 100%;
    background-repeat: no-repeat;
    background-size: 0% 1.5px;
    transition: all .5s ease-in;
    font-weight: bold;
}

.navbar-brand a:hover{
    color: #1e96ce !important;
    background-size: 100% 1.5px;
    text-decoration: none;
    cursor: pointer;
}

.navbar-light .navbar-nav .nav-link.active{
    color: #23b8ff;
    background-size: 100% 1.5px;
}
.navbar-light .navbar-nav .nav-link:hover, .navbar-dark{
    color: #23b8ff;
    background-size: 100% 1.5px;
    cursor: pointer;
}

